.container {
    position: relative;
    box-sizing: border-box;
}

.field {
    position: relative;
    width: calc(100% - 10px);
    height: 2.5rem;
    border-radius: 10px;
    box-shadow: inset 0 0 3px #000b;
    border: 2px solid #007CBF;
    box-sizing: border-box;
    background-color: #fff;
    z-index: 11;
    font-size: 1rem;
    padding-left: 10px;
    margin: 5px;
}

.field.active {
    border: 2px solid #00A6FF;
    box-shadow: inset 0 0 3px #000b, 0 0 3px #00a6ff80;
}

.field.validate {
    border: 2px solid #dd0000;
    box-shadow: inset 0 0 3px #800b, 0 0 3px #dd000080;
}

.label {
    position: absolute;
    pointer-events: none;
    font-size: 1rem;
    font-style: italic;
    line-height: 0.9rem;
    top: 1rem;
    left: 30px;
    color: #777;
    transition: top 0.15s;
    z-index: 12;
    margin: 0;
    padding: 0;
    border-radius: 2px;

}

.label.active {
    font-size: 1rem;
    font-style: normal;
    top: -0.1rem;
    color: #333;
    background-color: #fff;
    box-shadow: 0 0 2px 1px #fff;
    transition: top 0.15s;
}

.message {
    position: absolute;
    bottom: 0.1rem;
    right: 45px;
    z-index: 12;
    line-height: 0.9rem;
    color: #f00;
    background-color: #fff;
    box-shadow: 0 0 2px 1px #fff;
}

.button {
    width: 110px;
    height: 30px;
    border-radius: 8px;
    box-sizing: border-box;
    box-shadow: 0 0 3px #0004;
    font-size: 1rem;
}

.login {
    border: 1px solid #007CBF;
    background-color: #00A6FF;
    color: #fff;
}

.signup {
    border: 1px solid #007CBF;
    background-color: #002940;
    color: #fff;
}

.cancel {
    border: 1px solid #007CBF;
    background-color: #476576;
}

.submit {
    border: 2px solid #007CBF;
    background-color: #fff;
}

.left {
    margin-right: 5px;
}

.right {
    margin-left: 5px
}

.middle {
    margin: 0 5px;
}